import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'
import { editNote } from '../reducer/userReducer'

const EditNote = () => {

    const [title, setTitle] = useState("")
    const [description, setDescription] = useState("")
    const { id } = useParams();
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const { note } = useSelector(state => state.note);

    let myNote;
    note && note.forEach((item) => {
        if (item._id === id) {
            myNote = item;
        }
    })

    useEffect(() => {
        myNote && setTitle(myNote.title)
        myNote && setDescription(myNote.description)
    }, [myNote])

    const submitFormHandler = (e) => {
        e.preventDefault();
        dispatch(editNote(id, { title, description }));
        navigate("/");
    }


    return (
        <>
            <div className="registerContainer">
                <form onSubmit={submitFormHandler} className="registerForm" >
                    <h2>Edit Note</h2>
                    <label htmlFor="title">Title : </label>
                    <input
                        type="text"
                        id="title"
                        value={title}
                        placeholder="Enter Title"
                        onChange={(e) => setTitle(e.target.value)}
                    />
                    <label htmlFor="description">Description : </label>
                    <textarea
                        name="description"
                        id="description"
                        cols="30"
                        rows="10"
                        value={description}
                        placeholder="Enter Description"
                        onChange={(e) => setDescription(e.target.value)}
                    ></textarea>

                    <input type="submit" value="Update" />
                </form>
            </div>
        </>
    )
}

export default EditNote