import { createSlice } from '@reduxjs/toolkit';
import axios from 'axios';


export const userSlice = createSlice({
    name: "user",
    initialState: {
        user: {},
        error: null,
    },
    reducers: {
        login: (state, action) => {
            state.user = action.payload
        },
        register: (state, action) => {
            state.user = action.payload
        },
        loadUser: (state, action) => {
            state.user = action.payload
        },
        logoutUser: (state, action) => {
            state.user = {}
        },
        userError: (state, action) => {
            state.error = action.payload
        },
    }
})

export const noteSlice = createSlice({
    name: "newNote",
    initialState: {
        note: [],
        error: null,
    },
    reducers: {
        createNote: (state, action) => {
            state.note = action.payload
        },
        allNotes: (state, action) => {
            state.note = action.payload
        },
        removeNote: (state, action) => {
            state.note = action.payload
        },
        updateNote: (state, action) => {
            state.note = action.payload
        },
        noteError: (state, action) => {
            state.error = action.payload
        },
    }
})




export const userRegister = (userData) => async (dispatch) => {
    try {

        const config = { headers: { "Content-Type": "application/json" } };

        const { data } = await axios.post("/api/v1/register", userData, config);

        dispatch(register(data));

    } catch (error) {
        dispatch(userError(error.response.data.message));
    }
}

export const getLoadUser = () => async (dispatch) => {
    try {

        const { data } = await axios.post("/api/v1/me");

        dispatch(loadUser(data));

    } catch (error) {
        dispatch(userError(error.response.data.message));
    }
}

export const userLogin = (userData) => async (dispatch) => {
    try {
        const config = { headers: { "Content-Type": "application/json" } };

        const { data } = await axios.post("/api/v1/login", userData, config);

        dispatch(login(data));

    } catch (error) {
        dispatch(userError(error.response.data.message));
    }
}

export const userLogout = () => async (dispatch) => {
    try {
        await axios.post("/api/v1/logout");

        dispatch(logoutUser());

    } catch (error) {
        dispatch(userError(error.response.data.message));
    }
}



export const createNewNote = (noteData) => async (dispatch) => {
    try {

        const config = { headers: { "Content-Type": "application/json" } };

        const { data } = await axios.put("/api/v1/", noteData, config);

        dispatch(createNote(data.user.notes));

    } catch (error) {
        dispatch(noteError(error.response.data.message));
    }
}

export const getAllNotes = () => async (dispatch) => {
    try {

        const { data } = await axios.post("/api/v1/");

        dispatch(allNotes(data.notes));

    } catch (error) {
        dispatch(noteError(error.response.data.message));
    }
}

export const deleteNote = (id) => async (dispatch) => {
    try {

        const { data } = await axios.delete(`/api/v1/${id}`);

        dispatch(removeNote(data.notes));

    } catch (error) {
        dispatch(noteError(error.response.data.message));
    }
}

export const editNote = (id, noteData) => async (dispatch) => {
    try {
        const config = { headers: { "Content-Type": "application/json" } };

        const { data } = await axios.put(`/api/v1/${id}`, noteData, config);

        dispatch(updateNote(data.notes));

    } catch (error) {
        dispatch(noteError(error.response.data.message));
    }
}




export const { login, register, loadUser, userError, logoutUser } = userSlice.actions;
export const { createNote, allNotes, removeNote, updateNote, noteError } = noteSlice.actions;

export const user = (state) => state.user;
